import React from 'react';
import { Steps } from 'antd';


interface ItemType {
    title: string;
    description: string;
}

interface Props {
    current: number,
    items: ItemType[],
    size?: string
}

const ProcessTracker: React.FC = (props: Props) => (
    <Steps
        {...props}
    />
);

export default ProcessTracker;